.banner {
  background: url("https://tr-images.condecdn.net/image/jQ4o0E25AqM/crop/1020/f/180612_super-suite-bedrm-hr.jpg") center center;
  height: 50vh;
  position: relative;
  background-repeat: no-repeat;
  background-size: cover;
  margin-top: 10px;
}

.banner__searchButton {
  background-color: white !important;
  font-weight: 900 !important;
  text-transform: inherit !important;
  color: #ff7779 !important;
}

.banner__search {
  display: flex;
  flex-direction: column;
}

.banner__info {
  background-color: black;
  color: white;
  padding-top: 16vh;
  padding-left: 50px;
  padding-right: 50px;
  padding-bottom: 40px;
  width: 300px;
}

.banner__info > button {
  background-color: #ff7779;
  color: white;
  text-transform: inherit;
  margin-top: 20px;
  font-weight: 600;
}

.banner__info > button:hover {
  background-color: white;
  color: #ff7779;
}

.banner__info > h5 {
  margin-top: 10px;
}